/* wrapper */

.wrapper {
  margin: 0px auto;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 32px 36px;
  gap: 32px 36px;
  padding-bottom: 50px;
  padding-top: 50px;
}

.wrapper {
