  row-gap: 28px;
}

.wrapper:first-of-type {
  padding-top: 8rem;
}

.wrapper .col1-start {
  grid-column-start: 1;
}

.wrapper .col2-start {
  grid-column-start: 2;
}

.wrapper .col3-start {
  grid-column-start: 3;
}

.wrapper .col4-start {
  grid-column-start: 4;
}

.wrapper .col5-start {
