/* Burger */

.burger-menu,
.burger-menu.open {
  display: inline-block;
  cursor: pointer;
  position: relative;
  z-index: 9999;
  border-radius: 25px;
  background-color: transparent;
}

.burger-menu .bar1,
.burger-menu .bar2 {
  width: 30px;
  height: 1px;
  background-color: #000;
  margin: 7px 0;
  transition: 0.4s;
}

.burger-menu.open .bar1 {
  -webkit-transform: rotate(-30deg) translate(-4px, 4px);
  transform: rotate(-30deg) translate(-2px, 2px);
  background-color: #fff;
}

.burger-menu.open .bar2 {
  -webkit-transform: rotate(30deg) translate(-4px, -4px);
  transform: rotate(30deg) translate(-4px, -4px);
  background-color: #fff;
}

.Interactive .burger-menu .bar1,
.Interactive .burger-menu .bar2,
.Interactive .burger-menu.open bar1,
.Interactive .burger-menu.open bar2,
.About .burger-menu .bar1,
.About .burger-menu .bar2,
.About .burger-menu.open bar1,
.About .burger-menu.open bar2 {
  background-color: #fff;
}

.burger-menu {
  display: none;
}


